@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&family=Roboto+Slab:wght@400;500;700&display=swap);
:root {
  --azure: #0290ff;
  --black-two: #121212;
  --pale-gray: #9c9c9c;
  --warm-gray: #5a5959;
  --white: #e8e8e8;
  --pinkish-grey: #c4c4c4;
  --dodger-blue: #379cf6;
}

body {
  background-color: #fafafa;
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  padding: 2.313rem 6.25rem;
}

.line {
  width: 100%;
  margin: 2.5rem 0.063rem 1.813rem 0;
  border: solid 1px #e8e8e8;
  border: solid 1px var(--white);
  background-color: #e8e8e8;
  background-color: var(--white);
}

.flex {
  display: flex;
  align-items: center;
}

a {
  text-decoration: none;
  color: var(--black-two);
}

p {
  padding: 0;
  margin: 0;
}

.nav-container {
  background-color: #fff;
  padding: 1.813rem 6.25rem;
  justify-content: space-between;
  border-bottom: solid 1px var(--white);
}

.navbar {
  font-family: 'Montserrat', sans-serif;
}

.navbar p {
  padding-right: 2.938rem;
  font-size: 1.875rem;
  font-weight: bold;
  color: var(--azure);
}

.nav li {
  padding-right: 2.563rem;
  font-size: 0.813rem;
  letter-spacing: 1.9px;
}

.categories {
  opacity: 0.5;
}

.account {
  font-size: 2rem;
  color: var(--azure);
}

.form-title {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.25rem;
  font-weight: bold;
  color: #888;
  margin: 1.813rem 32.75rem 1.188rem 0;
}

input {
  height: 2.813rem;
  padding: 0.2rem 0 0.2rem 1rem;
  margin-right: 2.125rem;
  border-radius: 4px;
  border: solid 1px var(--white);
  outline: none;
  background-color: #fff;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  color: var(--pinkish-grey);
}

.title {
  width: 30rem;
}

.category {
  width: 17rem;
}

.submitBtn {
  width: 11.5rem;
  padding: 1.04rem;
  border-radius: 3px;
  border: none;
  outline: none;
  background-color: var(--azure);
  font-family: 'Roboto Slab', serif;
  font-size: 0.813rem;
  font-weight: bold;
  letter-spacing: 0.5px;
  text-align: center;
  color: #fff;
}

.flex {
  display: flex;
}

.Oval-2 {
  width: 4.25rem;
  height: 4.25rem;
  border-top: 5px solid var(--dodger-blue);
  border-right: 5px solid var(--dodger-blue);
  border-bottom: 5px solid var(--dodger-blue);
  border-left: 5px solid var(--white);
  border-radius: 50%;
  transform: rotate(45deg);
}

.progress {
  width: 16rem;
  border-right: solid 1px var(--white);
  margin-right: 3.68rem;
}

.text {
  flex-direction: column;
  align-items: flex-start;
  color: var(--black-two);
  margin-left: 1.4rem;
}

.percent {
  font-family: 'Montserrat', sans-serif;
  font-size: 2rem;
}

.status {
  opacity: 0.5;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.875rem;
}

.chap {
  font-family: 'Roboto Slab', serif;
  color: #121212;
}

.current {
  opacity: 0.5;
  font-size: 0.813rem;
  margin-bottom: 0.438rem;
}

.chapter {
  font-size: 1rem;
  letter-spacing: -0.4px;
  margin-bottom: 1.438rem;
}

.update {
  width: 11.5rem;
  padding: 0.5rem;
  border-radius: 3px;
  border: none;
  outline: none;
  background-color: var(--azure);
  font-size: 0.813rem;
  letter-spacing: 0.5px;
  text-align: center;
  color: #fff;
}

.card {
  width: 94%;
  height: 6rem;
  margin-bottom: 1rem;
  padding: 2rem;
  border-radius: 4px;
  border: solid 1px var(--white);
  background-color: #fff;
  align-items: center;
}

.flex {
  display: flex;
}

.book {
  width: 28rem;
}

.bookCategory {
  opacity: 0.5;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.875rem;
  font-weight: bold;
  color: var(--black-two);
}

.bookTitle {
  font-family: 'Roboto Slab', serif;
  font-size: 1.375rem;
  font-weight: bold;
  letter-spacing: -0.2px;
  color: var(--black-two);
  margin-bottom: 2rem;
}

.actions li {
  padding: 0 1rem 0;
  border-right: solid 1px var(--white);
  font-family: 'Roboto Slab', serif;
  font-size: 0.875rem;
  color: #4386bf;
}

.rmBtn {
  background-color: white;
  border: none;
  outline: none;
  font-family: 'Roboto Slab', serif;
  color: #4386bf;
  font-size: 0.875rem;
  font-weight: 400;
}

.actions li:last-child {
  border-right: none;
}

.actions li:first-child {
  padding-left: 0;
}

